@import url(https://fonts.googleapis.com/css?family=K2D);
html, body {
  height:100%!important;
  margin: 0;
  padding: 0;
}

#root{
  height:100%!important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    height: 100%;
    width: 100%;
}
.home-grid{
    background-image: url(/static/media/back.d7e40a0d.jpg);
    background-size: cover;
    flex: 1 1;
    width: 100%;
    height: 100%
}

.home-welcome-grid{
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-family: 'K2D', sans-serif;
    text-align: left;
}


.home-welcome-text-big{
    font-size: 70px;
}

.home-welcome-text-small{
    margin-top: -20px;
    font-size: 20px;
}

.home-welcome-grid p{
    color: white;
}

.home-see-button{
    margin-top: 10px;
    font-size: 15px;
    background-color: transparent;
    color: white;
    border: 2px solid #e7e7e7;
}

.home-see-button:hover{
    background-color: transparent!important;
    border: 2px solid #e7e7e7!important;
}

